import React from 'react'
import { Box } from 'theme-ui'
import Select from 'react-select'
import CreatableSelect from 'react-select/creatable'
import ErrorText from '../ErrorText'
import PropTypes from 'prop-types'
import theme from '../../theme'

/**
 * Used for selecting a single option from a defined set
 *
 * @visibleName Select Field
 */
const SelectField = ({
  name,
  placeholder,
  value,
  isFullWidth,
  handleChange,
  error,
  handleBlur,
  options,
  height,
  autoFocus,
  optionsAbove,
  allowCreateOptions,
  isSearchable,
  handleCreateOption,
  forceFocus
}) => {
  const isErrored = error && error.isError

  const getStyles = () => {
    const basicStyle = {
      boxSizing: 'border-box',
      display: 'flex',
      alignItems: 'center',
      flexWrap: 'wrap',
      justifyContent: 'space-between',
      position: 'relative',
      borderRadius: 8,
      borderStyle: 'solid',
      borderWidth: 1,
      fontFamily: '"Roboto", sans-serif',
      fontSize: theme.fontSizes[1],
      height: theme.rowHeight,
      letterSpacing: '0.01em',
      lineHeight: '38px',
      WebkitFontSmoothing: 'antialiased',
      outlineStyle: 'auto',
      outlineColor: 'navyGray',
      '&:hover': {
        outlineWidth: 2
      },
      '::-ms-expand': {
        display: 'none'
      },
      color: 'navyGray',
      gridArea: 'input',
      msGridColumn: '1',
      '&::placeholder': {
        color: 'darkGray'
      }
    }

    const baseStyles = {
      valueContainer: (provided) => ({
        ...provided,
        height: '100%',
        padding: '0 16px'
      }),
      input: (provided) => ({
        ...provided,
        width: isFullWidth ? '100%' : 'auto',
        margin: 0,
        paddingBottom: 0,
        paddingTop: 0
      }),
      dropdownIndicator: (provided) => ({
        ...provided,
        color: '#675D55'
      }),
      indicatorSeparator: (provided) => ({
        ...provided,
        display: 'none'
      }),
      menu: (provided, state) => ({
        ...provided,
        margin: 0,
        padding: 0,
        borderRadius: theme.space[2],
        overflow: 'hidden',
        bottom: state.selectProps.optionsAbove ? '100%' : provided.bottom,
        top: state.selectProps.optionsAbove ? 'auto' : provided.top
      }),
      menuList: (provided) => ({
        ...provided,
        margin: 0,
        padding: 0,
        borderRadius: theme.space[2],
        overflow: 'hidden',
        boxShadow: '0px 0px 16px rgba(132, 121, 112, 0.64)',
        maxHeight: `${height}px`,
        overflow: 'auto'
      }),
      option: (provided, state) => ({
        ...provided,
        fontFamily: '"Roboto", sans-serif',
        fontSize: theme.fontSizes[1],
        paddingTop: (theme.rowHeight - 22) / 2,
        paddingBottom: (theme.rowHeight - 22) / 2,
        lineHeight: '22px',
        margin: 0,
        padding: '0 16px',
        color: state.isSelected ? 'white' : '#675D55'
      }),
      placeholder: (provided) => ({
        ...provided,
        fontFamily: '"Roboto", sans-serif',
        fontSize: theme.fontSizes[1],
        color: '#675D55',
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden'
      })
    }

    if (isErrored) {
      return {
        ...baseStyles,
        control: (provided, state) => ({
          ...basicStyle,
          outlineWidth: state.isFocused ? 2 : 0,
          borderColor: 'red',
          backgroundColor: 'lightRed',
          // HACK: colorize webkit autocomplete input fields
          WebkitBoxShadow: `inset 0 0 0 1px rgba(255, 255, 255, 0), inset 0 0 0 100px #FEECEC`
        })
      }
    }
    return {
      ...baseStyles,
      control: (provided, state) => ({
        ...basicStyle,
        outlineWidth: state.isFocused || forceFocus ? 2 : 0,
        borderColor: 'gray',
        // HACK: colorize webkit autocomplete input fields
        boxShadow:
          'inset 0 0 0 1px rgba(255, 255, 255, 0), inset 0 0 0 100px white'
      })
    }
  }

  const SelectComponent = allowCreateOptions ? CreatableSelect : Select

  return (
    <Box>
      <Box
        sx={{
          position: 'relative',
          display: isFullWidth ? 'block' : ['block', 'grid'],
          gridTemplateColumns: ['auto', '5fr 4fr'],
          gridTemplateAreas: "'input spacer'",
          '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
            display: isFullWidth ? 'block' : '-ms-grid',
            msGridColumns: '5fr 4fr'
          }
        }}
      >
        <SelectComponent
          name={`select-field-${name}`}
          isSearchable={isSearchable}
          options={options}
          value={value}
          styles={getStyles()}
          onChange={(value) => {
            handleChange && handleChange(name, value)
            handleBlur && handleBlur(name, { hasInteracted: true })
          }}
          placeholder={placeholder}
          autoFocus={autoFocus}
          optionsAbove={optionsAbove}
          onCreateOption={handleCreateOption}
        />
        <Box sx={{ gridArea: 'spacer', msGridColumn: '2' }} />
      </Box>
      <ErrorText error={error} mb={3} />
    </Box>
  )
}

SelectField.propTypes = {
  /** An array of options for the select field  */
  options: PropTypes.array,
  /** The maximum number of options to show before scrolling  */
  maxOptions: PropTypes.number,
  /** When this is true, the select field will render the options above the input field */
  optionsAbove: PropTypes.bool,
  /** Select field label for accessibility */
  name: PropTypes.string,
  /** Placeholder field */
  placeholder: PropTypes.string,
  /** A callback to fire when the select field changes */
  handleChange: PropTypes.func,
  /** A callback to fire when the select field loses focus */
  handleBlur: PropTypes.func,
  /** A callback to fire when the select field adds a new option */
  handleCreateOption: PropTypes.func,
  /** A value for the select field - this should be an object */
  value: PropTypes.object,
  /** When this is true, the select field will render full width */
  isFullWidth: PropTypes.bool,
  /** When this is true, the select field will be searchable */
  isSearchable: PropTypes.bool,
  /** When this is true, the user will be allowed to create new options */
  allowCreateOptions: PropTypes.bool,
  /** When this is true, the select field will focus on mount */
  autoFocus: PropTypes.bool,
  /** When this is true, the select field will render to appear as it is in focus */
  forceFocus: PropTypes.bool,
  /** An object describing the error in the select field */
  error: PropTypes.shape({
    isError: PropTypes.bool,
    hasInteracted: PropTypes.bool,
    message: PropTypes.string
  })
}

SelectField.defaultProps = {
  name: 'select-field',
  value: null,
  maxOptions: 5,
  options: [],
  error: {},
  isFullWidth: false,
  isSearchable: true,
  allowCreateOptions: false,
  forceFocus: false
}

export default SelectField
